<template>
  <div class="component">
    <div class="box">
      <div class="col">
        <div class="box rowBox">
          <div class="hyTitle">유저</div>
          <v-dialog v-model="dialog" max-width="500px">
            <template v-slot:activator="{ on, attrs }">
              <v-btn color="primary" dark v-bind="attrs" v-on="on">
                New Item
              </v-btn>
            </template>
            <v-card>
              <v-card-title>
                <span class="text-h5">{{ formTitle }}</span>
              </v-card-title>
              <v-card-text>
                <v-container>
                  <v-row>
                    <v-col cols="12" sm="6" md="4">
                      <v-text-field
                        v-model="editedItem.shipname"
                        label="shipname"
                      ></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      <v-text-field
                        v-model="editedItem.equipid"
                        label="equipid"
                      ></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      <v-text-field
                        v-model="editedItem.tagname"
                        label="tagname"
                      ></v-text-field>
                    </v-col>
                    <v-col cols="12" sm="6" md="4">
                      <v-text-field
                        v-model="editedItem.tagdesc"
                        label="tagdesc"
                      ></v-text-field>
                    </v-col>
                  </v-row>
                </v-container>
              </v-card-text>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="close">
                  Cancel
                </v-btn>
                <v-btn color="blue darken-1" text @click="save"> Save </v-btn>
              </v-card-actions>
            </v-card>
          </v-dialog>
          <v-dialog v-model="dialogDelete" max-width="500px">
            <v-card>
              <v-card-title class="text-h5"
                >Are you sure you want to delete this item?</v-card-title
              >
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="blue darken-1" text @click="closeDelete"
                  >Cancel</v-btn
                >
                <v-btn color="blue darken-1" text @click="deleteItemConfirm"
                  >OK</v-btn
                >
                <v-spacer></v-spacer>
              </v-card-actions>
            </v-card>
          </v-dialog>

          <div class="spacer"></div>
          <v-select
            v-model="ship"
            :items="ships"
            :item-text="(item) => item.shipname"
            menu-props="auto"
            label="선박"
            hide-details
            prepend-icon="mdi-ferry"
            single-line
          >
          </v-select>
          <v-text-field
            v-model="refTableSearch"
            append-icon="mdi-magnify"
            label="검색"
            single-line
            hide-details
          ></v-text-field>
        </div>
        <div class="box">
          <v-data-table
            fixed-header
            :headers="refTableHeaders"
            :items="refTableList"
            :items-per-page="-1"
            item-key="name"
          >
            <template v-slot:[`item.actions`]="{ item }">
              <v-icon small class="mr-2" @click="editItem(item)">
                mdi-pencil
              </v-icon>
              <v-icon small @click="deleteItem(item)"> mdi-delete </v-icon>
            </template>
          </v-data-table>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import tableList from "../assets/list.json";
import { ref, watch, computed, nextTick } from "vue";
// import { useAuthStore } from "@/stories/mainStore.js";

export default {
  name: "RegComponent",
  components: {},
  props: {
    api: String,
  },
  setup(props) {
    console.log(props.api);

    //
    const ships = ref([
      { shipid: "1", shipname: "아리온제주1" },
      { shipid: "2", shipname: "아리온제주2" },
      { shipid: "3", shipname: "아리온제주3" },
      { shipid: "4", shipname: "아리온제주4" },
      { shipid: "5", shipname: "아리온제주5" },
      { shipid: "5", shipname: "아리온제주6" },
      { shipid: "5", shipname: "아리온제주7" },
      { shipid: "5", shipname: "아리온제주8" },
      { shipid: "5", shipname: "아리온제주9" },
    ]);
    const ship = ref(null);

    const refTableSearch = ref("");
    const refTableList = ref(Array.from(tableList));
    const refTableHeaders = [
      // { text: "", align: "center", value: "glutenfree" },
      {
        text: "NO",
        align: "center",
        value: "no",
      },
      { text: "선박", align: "center", value: "shipname" },
      { text: "장비ID", align: "center", value: "equipid" },
      { text: "태그", align: "center", value: "tagname" },
      { text: "Description", align: "center", value: "tagdesc" },
      { text: "Actions", value: "actions", sortable: false },
    ];

    //
    const dialog = ref(false);
    const dialogDelete = ref(false);

    const editedIndex = ref(-1);
    const editedItem = ref({
      // no: 0,
      shipname: "",
      equipid: "",
      tagname: "",
      tagdesc: "",

      // name: "",
      // calories: 0,
      // fat: 0,
      // carbs: 0,
      // protein: 0,
    });
    const defaultItem = ref({
      // no: 0,
      shipname: "",
      equipid: "",
      tagname: "",
      tagdesc: "",
    });
    const editItem = (item) => {
      editedIndex.value = refTableList.value.indexOf(item);
      editedItem.value = Object.assign({}, item);
      dialog.value = true;
    };

    const deleteItem = (item) => {
      editedIndex.value = refTableList.value.indexOf(item);
      editedItem.value = Object.assign({}, item);
      dialogDelete.value = true;
    };

    const deleteItemConfirm = () => {
      refTableList.value.splice(editedIndex.value, 1);
      closeDelete();
    };

    const close = () => {
      dialog.value = false;
      nextTick(() => {
        editedItem.value = Object.assign({}, defaultItem.value);
        editedIndex.value = -1;
      });
    };

    const closeDelete = () => {
      dialogDelete.value = false;
      nextTick(() => {
        editedItem.value = Object.assign({}, defaultItem.value);
        editedIndex.value = -1;
      });
    };

    const save = () => {
      if (editedIndex.value > -1) {
        Object.assign(refTableList.value[editedIndex.value], editedItem.value);
      } else {
        refTableList.value.push(editedItem.value);
      }
      close();
    };

    watch(dialog, (val) => {
      val || close();
    });

    watch(dialogDelete, (val) => {
      val || closeDelete();
    });

    const formTitle = computed(() => {
      return editedIndex.value === -1 ? "New Item" : "Edit Item";
    });

    return {
      formTitle,

      dialog,
      dialogDelete,
      refTableHeaders,
      refTableList,
      editedIndex,
      editedItem,
      defaultItem,
      editItem,
      deleteItem,
      deleteItemConfirm,
      close,
      closeDelete,
      save,

      refTableSearch,
      //
      ships,
      ship,
    };
  },
};
</script>

<style></style>
